@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-display: swap;
  src: local("Opensans regular"), url("../fonts/opensans.woff") format("woff"), url("../fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-display: swap;
  src: local("Opensans bold"), url("../fonts/opensansbold.woff") format("woff"), url("../fonts/opensansbold.woff2") format("woff2");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  color: #444444;
  background-color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.6;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}
.overlay {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  animation-name: appearance-overlay;
  animation-duration: 1.2s;
  opacity: 0.9;
  background-color: #ffffff;
}
.overlay--open {
  display: block;
}
.container {
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 30px;
  padding-left: 30px;
}
@media (min-width: 768px) {
  .container {
    padding-right: 50px;
    padding-left: 50px;
  }
}
@keyframes appearance {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes appearance-overlay {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.9;
  }
}
.btn {
  display: block;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.2s linear;
  text-align: center;
  text-transform: uppercase;
  color: #444444;
  border: 2px solid #444444;
  background-color: transparent;
  font: inherit;
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
}
.btn:hover {
  color: #ffffff;
  background-color: #444444;
}
.btn:focus {
  color: #ffffff;
  background-color: #444444;
  outline: none;
}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
}
.header {
  position: relative;
}
@media (min-width: 768px) {
  .header {
    background-color: #f9f9f9;
  }
  .header--inner {
    background-color: #f1f1f1;
  }
  .header::after {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 89px;
    content: "";
    border-bottom: 1px solid #ececec;
    background-color: #ffffff;
  }
}
.header__logo {
  transition: opacity 0.2s linear;
}
.header__logo:hover {
  opacity: 0.6;
}
.header__logo:focus {
  opacity: 0.6;
}
.header__logo:active {
  opacity: 0.3;
}
.header__logo-img {
  width: 86px;
  height: 35px;
}
@media (min-width: 768px) {
  .header__logo-img {
    width: 112px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .header__logo-img {
    width: 149px;
    height: 72px;
  }
}
.main-nav {
  position: relative;
  z-index: 2;
  max-width: 1300px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .main-nav--close .main-nav__nav,
  .main-nav--close .main-nav__menu {
    display: none;
  }
  .main-nav--close .main-nav__toggle::before {
    top: 4px;
    left: 50%;
    width: 20px;
    height: 2px;
    transform: translateX(-50%);
    box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
  }
  .main-nav--close .main-nav__toggle::after {
    content: none;
  }
  .main-nav--nojs .main-nav__nav,
  .main-nav--nojs .main-nav__menu {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
.main-nav__logo-wrap {
  position: relative;
  order: 1;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav__logo-wrap {
    display: flex;
    flex-grow: 1;
    width: 40%;
    height: 90px;
    border: none;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo-wrap {
    width: 250px;
  }
}
.main-nav__logo {
  display: block;
  width: 100%;
  padding: 22px 0;
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav__logo {
    padding-left: 50px;
    text-align: left;
    vertical-align: middle;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo {
    padding: 9px 0 9px 45px;
  }
}
.main-nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    position: absolute;
    top: 50%;
    right: 30px;
    display: block;
    width: 21px;
    height: 21px;
    padding: 0;
    cursor: pointer;
    transform: translateY(-50%);
    border: none;
    background-color: transparent;
  }
  .main-nav__toggle::before,
  .main-nav__toggle::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 28px;
    height: 3px;
    content: "";
    background-color: #000000;
  }
  .main-nav__toggle::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .main-nav__toggle::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}
.main-nav__nav {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__nav {
    display: flex;
    order: 3;
    margin-left: 50px;
  }
}
@media (min-width: 1150px) {
  .main-nav__nav {
    order: -1;
  }
}
.main-nav__nav-item {
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav__nav-item {
    border: none;
  }
}
@media (min-width: 1150px) {
  .main-nav__nav-item:not(:last-child) {
    margin-right: 34px;
    border-right: 1px solid #ececec;
  }
}
.main-nav__nav-link {
  display: block;
  box-sizing: border-box;
  max-width: 320px;
  margin: 0 auto;
  padding: 25px 0 25px 74px;
  transition: 0.2s linear;
  color: #000000;
  font-size: 17px;
  font-weight: 700;
}
.main-nav__nav-link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__nav-link:focus {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__nav-link:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .main-nav__nav-link {
    width: 167px;
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .main-nav__nav-link {
    padding: 16px 0;
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .main-nav__nav-link {
    width: 185px;
    padding: 31px 0;
  }
}
.main-nav__menu {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__menu {
    display: flex;
    order: 2;
    margin-right: 50px;
    border-left: 1px solid #ececec;
  }
}
.main-nav__menu-item {
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav__menu-item {
    border: none;
  }
  .main-nav__menu-item--search {
    border-right: 1px solid #ececec;
  }
}
.main-nav__menu-link {
  position: relative;
  display: block;
  box-sizing: border-box;
  max-width: 320px;
  margin: 0 auto;
  padding: 26px 0 26px 74px;
  color: #000000;
  font-size: 17px;
  font-weight: 700;
}
.main-nav__menu-link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__menu-link:hover .main-nav__menu-img {
  fill: #63d1bb;
}
.main-nav__menu-link:focus {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__menu-link:focus .main-nav__menu-img {
  fill: #63d1bb;
}
.main-nav__menu-link:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__menu-link:active .main-nav__menu-img {
  fill: rgba(99, 209, 187, 0.3);
}
@media (min-width: 768px) {
  .main-nav__menu-link {
    padding: 31px 0 31px 82px;
  }
}
.main-nav__menu-img {
  position: absolute;
  top: 50%;
  left: 30px;
  transition: 0.2s linear;
  transform: translateY(-50%);
  fill: #d3d2d2;
}
.main-nav__news {
  display: none;
  margin: 0;
  padding: 0 0 0 50px;
  list-style: none;
}
@media (min-width: 1150px) {
  .main-nav__news {
    display: flex;
    order: 3;
  }
}
.main-nav__news-link {
  padding-right: 60px;
  transition: 0.2s linear;
  color: #000000;
  font-size: 15px;
  font-weight: 700;
}
.main-nav__news-link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__news-link:focus {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__news-link:active {
