}
.main-nav__text {
  display: none;
  margin: 0 50px 0 0;
  font-size: 15px;
}
@media (min-width: 768px) {
  .main-nav__text {
    display: block;
    flex-grow: 1;
    order: 4;
    padding: 16px 0;
